<template>
  <div>

    <!--数据显示区域-->
    <el-card style="margin-top: 20px">
      <el-row>
        <el-col :span="24">
          <!-- 新增按钮 -->
          <el-header style="background-color: #ffffff; margin-left: -5px">
            <el-form class="query-form" inline size="mini" :label-width="120" :model="searchProduct">
              <el-form-item>
                <el-input placeholder="" class="input" @focus="" style="border-radius: 14px !important;">
                  <el-button slot="append" icon="el-icon-search"></el-button>
                </el-input>
              </el-form-item>
            </el-form>
            <div style="width: 300px;float: right;">
              <el-button @click="handleSave()" icon="el-icon-circle-plus-outline">新增项目</el-button>
              <el-button @click="handleSave()" icon="el-icon-circle-plus-outline" type="warning" plain>批量删除</el-button>
            </div>
          </el-header>
          <!-- 项目详细信息 -->
          <el-table :data="dataList" style="width: 100%" class="select-from" :stripe="true">
            <ElTableColumn type="selection" />
            <el-table-column sortable width="100" prop="id" label="ID"> </el-table-column>
            <el-table-column align="center" sortable prop="name" label="项目名称"> </el-table-column>
            <el-table-column align="center" prop="type" label="问题类型">
              <template slot-scope="scope">
                <span>{{ scope.label }}<i class="co-icon-filter" /></span>
              </template>
            </el-table-column>
            <el-table-column label="操作" width="300">
              <template slot-scope="scope">
                <el-button style="margin: 0 0 0 0" icon="el-icon-setting" size="mini" type="primary"
                  @click="setDialog(scope.row.id, scope.row.title, scope.row.price)" slot="reference">详情
                </el-button>
                <el-popconfirm :title="'确定删除' + scope.row.title + '吗？'" @confirm="remove(scope.row.id)">
                  <el-button style="margin: 0 0 0 15px" icon="el-icon-close" size="mini" type="danger" slot="reference">删除
                  </el-button>
                </el-popconfirm>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
      </el-row>

      <!--分页-->
      <el-row class="page-row">
        <el-pagination class="el-pagination" layout="total, sizes, prev, pager, next, jumper"
          :page-sizes="[5, 10, 30, 50]" @size-change="sizeChange" @current-change="currentChange"
          @prev-click="currentChange" @next-click="currentChange" :current-page="current" :page-size="size"
          :total="total">
        </el-pagination>
      </el-row>
    </el-card>

    <!-- 添加 -->
    <el-dialog title="添加项目" :visible.sync="saveDialog" width="800px">



      <el-form :model="productObj" ref="ruleForm" label-width="80px">
        <el-form-item label="项目名称" prop="title" :rules="[{ required: true, message: '名称不能为空' }]">
          <el-input v-model="productObj.title" maxlength="8" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="价格(分)" prop="price" :rules="[
          { required: true, message: '价格不能为空' },
          { type: 'number', message: '价格必须为数字值' },
        ]">
          <el-input v-model.number="productObj.price"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" style="width: 255px; margin: 45px 0 0 65px" @click="productAdd"
            :disabled="productObj.title === '' || productObj.price === null || productSet.price === ''">立即添加
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!-- 详情 -->
    <el-dialog title="项目详情" :visible.sync="updateDialog" width="600px">
      <span slot="title" class="dialog-footerB">
        <el-button size="mini" @click="goBack" icon="el-icon-arrow-left">项目详情</el-button>
        <div style="margin-top: 15px;">
          <div style="display: inline;display: flex;justify-content: space-between;">
            <span>项目名称:{{ }}</span>
            <div style="width: 240px;float: right;">
              <el-input style="width: 100PX;margin-right: 20px;" type="text" size="mini" placeholder="问题类型" suffix-icon="el-icon-search"></el-input>
              <el-input style="width: 100PX;margin-right: 20px;" type="text" size="mini" placeholder="问题id" suffix-icon="el-icon-search"></el-input>
            </div>
     
          </div>
        </div>
        <hr />
      </span>
      <el-form :model="productSet" ref="ruleForm" label-width="80px">
        <!-- <el-form-item label="项目ID" prop="id">
          <el-input v-model="productSet.id" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="项目名称" prop="title" :rules="[{ required: true, message: '名称不能为空' }]">
          <el-input v-model="productSet.title" maxlength="8" show-word-limit></el-input>
        </el-form-item> -->
        <el-form-item label="问题详情:" prop="description">
          <el-input v-model.number="productSet.description" type="textarea"></el-input>
        </el-form-item>
        <el-form-item label="修正意见:" prop="fix">
          <el-input v-model.number="productSet.price" type="textarea"></el-input>
        </el-form-item>
        <el-form-item label="代码范例:" prop="example">
          <el-input v-model.number="productSet.price" type="textarea"></el-input>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

