<style>
.page-row {
  margin-top: 20px;
  text-align: center;
}

.header2 {
  color: #004b54;
  font-size: 20px;
  position: absolute;
  left: 10px;
  top: 5px;
}

.action-groups {
  display: inline-block;
}


/*搜索组件最外层div */
.input_box {
  width: 300px;
  margin-right: 15px;
  border-radius: 95px;
  background: rgb(0 234 245 / 48%);
}

/*搜索input框 */
:deep(.el-input__wrapper) {
  background-color: transparent;
  /*覆盖原背景颜色，设置成透明 */
